.cart_main_no_items {
    min-height: calc(100vh - (68.05px + 42.4px));
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    font-size: 2.5rem;
  }
  .cart_main_empty {
    width: 100%;
    display: flex;
    justify-content: center;
    gap: 2rem;
    margin-bottom: 1.2rem;
  }
  .cart_main_no_items h2 {
    font-size: 2.2rem;
    text-transform: capitalize;
  }
  
  .cart_main_no_items p {
    font-size: 2rem;
  }
  .cart_main_no_items p a {
    text-decoration: underline;
  }
  
  .cart_main {
    padding: 1.5rem 0;
    min-height: 80vh;
  }
  .cart_main_header {
    border-bottom: 1.5px solid rgba(0, 0, 0, 0.2);
    padding-bottom: 1rem;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
  }
  
  .cart_main_header h1:last-child {
    text-align: center;
  }
  
  .cart_main h1 {
    font-size: 2rem;
  }
  
  .cart_article {
    display: grid;
    justify-content: space-between;
    grid-template-columns: 1fr 1fr 1fr;
    margin: 1rem 0;
    padding-bottom: 1.5rem;
    border-bottom: 1.5px solid rgba(0, 0, 0, 0.2);
  }
  
  .cart_figure_img {
    width: 75%;
    object-fit: cover;
    aspect-ratio: 1/ 0.65;
    border-radius: 2px;
  }
  
  .cart_section_1 h2 {
    color: rgba(0, 0, 0, 0.8);
    font-size: 2.4rem;
    text-transform: capitalize;
  }
  .cart_section_1 p {
    color: rgba(0, 0, 0, 0.7);
    font-size: 1.6rem;
    text-transform: capitalize;
  }
  .cart_section_2 h3 {
    font-size: 2rem;
    color: rgba(255, 0, 0, 0.9);
    text-align: center;
  }
  
  .cart_btn_container {
    max-width: 140px;
    width: calc(100px + 5vw);
    text-align: center;
    margin-top: 1.2rem;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 1.6rem;
    border: 1px solid rgba(128, 128, 128, 0.2);
    border-radius: 5px;
    overflow: hidden;
  }
  .cart_btn_container button {
    color: rgba(0, 0, 0, 0.9);
    font-size: 1.25rem;
    border: none;
    padding: 0.6rem 1.2rem;
    cursor: pointer;
    transition: 0.1s ease;
  }
  .cart_btn_container span {
    flex: 1;
    transform: translateY(-2px);
  }
  .cart_btn_container button:active {
    background-color: black;
    color: white;
  }
  
  .cart_footer_main {
    display: flex;
    flex-direction: row-reverse;
    align-items: end;
    justify-content: space-between;
    gap: 4.5rem;
  }
  
  .cart_footer_1 {
    width: 50%;
  }
  
  .cart_footer_2 {
    width: 50%;
  }
  .cart_footer_1_discount_card_container {
    display: flex;
    flex-direction: column;
    gap: 1.5rem;
    align-items: flex-start;
    justify-content: space-between;
    border-left: 2px solid rgba(0, 0, 0, 0.2);
    padding-right: 2rem;
  }
  .cart_footer_1_card {
    box-shadow: rgba(149, 157, 165, 0.1) 0px 8px 24px;
    padding: 15px 25px;
    border-radius: 8px;
    border: 1px solid rgba(0, 0, 0, 0.1);
    display: grid;
    grid-template-columns: 2.5fr 1fr;
    grid-template-rows: auto;
    column-gap: 2.5rem;
    row-gap: 0.6rem;
    margin-left: auto;
    width: 80%;
  }
  .cart_footer_1 h3 {
    color: rgba(0, 0, 0, 0.9);
    margin-bottom: 0.5rem;
    font-size: 1.25rem;
    letter-spacing: 1px;
  }
  
  .cart_footer_1 p {
    font-size: 1.2rem;
    letter-spacing: 1px;
  }
  .cart_footer_1_coupon_view_para {
    grid-column-start: 1;
    grid-column-end: 3;
  }
  .cart_footer_1_btn button {
    padding: 0.4rem 1rem;
    background-color: transparent;
    border-radius: 8px;
    color: rgba(204, 2, 2, 0.9);
    border: 1px solid rgba(204, 2, 2, 0.9);
    outline: none;
    cursor: pointer;
  }
  
  .cart_footer_1_btn button:active {
    background-color: rgba(204, 2, 2, 0.9);
    color: white;
  }
  
  .cart_place_order_btn_container {
    display: block;
    font-weight: 600;
    padding: 15px 0;
    border-radius: 8px;
    background-color: #f4ce14;
    border: none;
    outline: none;
    cursor: pointer;
    transition: var(--transition);
    width: 80%;
    text-align: center;
    margin-left: auto;
  }
  .cart_place_order_btn {
    font-size: 2rem;
    font-weight: 600;
    border: none;
    outline: none;
    background-color: transparent;
    color: rgba(0, 0, 0, 0.9);
    letter-spacing: 1px;
    cursor: pointer;
  }
  .cart_place_order_btn_container:hover,
  .place_order_confirm_btn_container button:hover {
    filter: brightness(0.8);
  }
  
  .cart_footer_2 .cart_order_summary {
    font-size: 2rem;
    color: rgba(0, 0, 0, 0.9);
  }
  .cart_footer_items_cal_container {
    padding: 0.5rem 0;
    border-bottom: 1px solid rgba(0, 0, 0, 0.2);
  }
  
  .cart_footer_items_cal_container div,
  .cart_footer_grand_total {
    display: grid;
    grid-template-columns: 2.5fr 0.5fr;
    gap: 2rem;
    margin: 0.6rem 0;
    padding: 0.4rem 0;
    font-size: 1.4rem;
    color: rgba(0, 0, 0, 0.6);
  }
  
  .cart_footer_items_cal_container div h3 {
    font-weight: 500;
  }
  
  .cart_footer_grand_total h2 {
    font-size: 2rem;
  }
  
